/* badge */
/* poster */
.poster-horizontal {
  position: relative;
  width: 253px; }
  .poster-horizontal .poster-thumbnail {
    overflow: hidden;
    position: relative;
    height: 142px;
    border-radius: 5px;
    background-image: linear-gradient(169deg, #595d72 -33%, #251d3e 80%);
    background-position: 50% 50%;
    background-size: cover; }
  .poster-horizontal .poster-component {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
    .poster-horizontal .poster-component::before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 89px;
      background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), #000); }
  .poster-horizontal .poster-tag {
    opacity: 0;
    position: absolute;
    top: 6px;
    right: 6px;
    display: flex;
    align-items: center; }
    .poster-horizontal .poster-tag li {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      margin-left: 3px; }
    .poster-horizontal .poster-tag-device span {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 25px; }
    .poster-horizontal .poster-tag-device .view-mobile {
      padding: 0 5px;
      border-radius: 5px;
      font-size: 1.8rem;
      color: #ef3484;
      background-color: rgba(0, 0, 0, 0.7); }
    .poster-horizontal .poster-tag-device .pay-mobile {
      width: 108px;
      background: url("../images/common/badge-pay-mobile.png") 50% 50% no-repeat;
      background-size: cover;
      font-size: 0;
      color: rgba(0, 0, 0, 0); }
    .poster-horizontal .poster-tag-device .view-all {
      width: 117px;
      background: url("../images/common/badge-view-all.png") 50% 50% no-repeat;
      background-size: cover;
      font-size: 0;
      color: rgba(0, 0, 0, 0); }
    .poster-horizontal .poster-tag-age {
      width: 28px;
      height: 28px;
      border-radius: 50%;
      font-size: 1.8rem;
      color: #eee;
      background-color: rgba(0, 0, 0, 0.7); }
      .poster-horizontal .poster-tag-age strong {
        color: #f42c3c; }
  .poster-horizontal .poster-time {
    position: absolute;
    bottom: 8px;
    right: 8px;
    left: 8px; }
    .poster-horizontal .poster-time dt {
      opacity: 0;
      position: relative;
      padding-left: 20px;
      margin-bottom: 5px;
      font-size: 2.6rem;
      color: #c3c3c3; }
      .poster-horizontal .poster-time dt::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: 14px;
        height: 14px;
        margin: auto 0;
        background: url("../images/common/ic-clock.png") 50% 50% no-repeat;
        background-size: cover;
        font-size: 0;
        color: rgba(0, 0, 0, 0); }
    .poster-horizontal .poster-time dd {
      position: relative;
      width: 100%;
      height: 4px;
      border-radius: 2px;
      background-color: #3a3a3a; }
      .poster-horizontal .poster-time dd span {
        position: absolute;
        height: 100%;
        border-radius: 5px;
        background-color: #ef3484; }
  .poster-horizontal .poster-title {
    position: absolute;
    bottom: -37px;
    left: 0;
    font-size: 3rem;
    color: #eee; }
  .focus .poster-horizontal::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    box-shadow: 0 0 0 4px #eee;
    border-radius: 5px; }
  .focus .poster-horizontal::after, .focus .poster-horizontal .poster-thumbnail, .focus .poster-horizontal .poster-component {
    transform: scale(1.06);
    transform-origin: left; }
  .focus .poster-horizontal .poster-tag, .focus .poster-horizontal .poster-time dt {
    opacity: 1; }

.poster-vertical {
  position: relative;
  width: 176px; }
  .poster-vertical .poster-thumbnail {
    overflow: hidden;
    position: relative;
    height: 250px;
    border-radius: 5px;
    background-image: linear-gradient(169deg, #595d72 -33%, #251d3e 80%);
    background-position: 50% 50%;
    background-size: cover; }
  .focus .poster-vertical {
    transform: scale(1.06);
    transform-origin: left; }
    .focus .poster-vertical::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      box-shadow: 0 0 0 4px #eee;
      border-radius: 5px; }

/* toast 임시 */
.toast {
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  height: 100%;
  padding-bottom: 66px; }
  .toast div {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 586px;
    min-height: 68px;
    padding: 16px 40px;
    text-align: center;
    font-size: 3.2rem;
    color: #080d19;
    border-radius: 13px;
    background-color: #eee;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.3); }
    .toast div strong {
      font-family: "YOONGOTHICPRO770"; }
